    font-family: sans-serif;
} */

.tela{
    width: 100%;
    margin: 0 auto;
}

.tela-login{
    width: 100%;
    min-height: 100vh;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    padding: 1rem;

    background-color: #1B1B1B;
}

.fundo-login{
    width: 390px;
    background-color: #0D0D0D;
    border-radius: 10px;

    overflow: hidden;
    padding: 77px 45px 33px 45px;

    box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.2);
}

.login-form{
    width: 100%;
}

.login-form-title{
    display: block;
    font-size: 30px;
    color: azure;                               

    line-height: 1.2;
    text-align: center;
}

.login-form-title img{
    width: 90px;       /* titulo em cima do logo */
    margin-bottom: 20px;
}

.estilo-input{

    width: 100%;
    position: relative;
    border-bottom: 2px solid #4A62BD;
    margin-bottom: 37px;

}

.estilo-input img{
    position: absolute;
    top: 50%;
    right: 3px;
    transform: translateY(-50%);
    background-size: cover;
    width: 20px;
    height: 18px;
}

.input{
    font-size: 15px;
    color: #4A62BD;
    line-height: 1.2;
    border: none;
    display: block;

    width: 100%;
    height: 45px;

    background-color: transparent;

    padding: 0 5px;
}
.icone{
    color: #4A62BD;
    font-weight: bold;
    
}
.focus-input{
    font-weight: bold;
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;

    top: 0;
    left: 0;

    pointer-events: none;
    color: #4A62BD;
}

.focus-input::before{
    content: "";
    display: block;
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0;
    height: 0; 

    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;
}

.focus-input::after{
    font-size: 15px;
    color: #999999;
    line-height: 1.2;

    content: attr(data-placeholder);

    display: block;
    width: 100%;
    position: absolute;
    top: 16px;
    left: 0px;

    padding-left: 5px; 

    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s; 
}

.input:focus{
    outline: 0;
}

 .input:focus + .focus-input::after{
    top: -15px;
}
/* todos esses input são da tansição do usuário e senha para cima quando são clicados */
.input:focus + .focus-input::before{
    width: 100%;
} 

.has-val  + .focus-input::after{
    top: -15px;
}

.esconder{
    display:none;
}
.fundo-btn{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 13px;
}

.login-btn{
    font-weight: bold;
    font-size: 15px;
    border: none;
    border-radius: 10px;
    color: #fff;
    text-decoration: none;

    line-height: 1.2;

    text-transform: uppercase;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 50px;

    background: #4A62BD;
}

.login-form-btn:hover{
    cursor: pointer;
}

.text-center{
    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: 50px;
}

.txt1{
    font-size: 14px;
    color: #adadad;
    line-height: 1.5;
    padding-right: 5px;
    text-decoration: none;
}

.txt2{
    font-size: 14px;
    color: #4A62BD;
    line-height: 1.5;
    text-decoration: none;
}


